'use client';
import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { createClient } from '@supabase/supabase-js';
import NeuralHalo from '@/components/NeuralHalo';
import IntelFeed from '@/components/IntelFeed';
import TrendPanel from '@/components/TrendPanel';
import CategoryPanel from '@/components/CategoryPanel';

const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL!;
const supabaseAnonKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!;
const supabase = createClient(supabaseUrl, supabaseAnonKey);

export default function Home() {
  const [formOpen, setFormOpen] = useState(false);
  const [category, setCategory] = useState('Countries');
  const [headline, setHeadline] = useState('');
  const [summary, setSummary] = useState('');
  const [region, setRegion] = useState('');
  const [sentiment, setSentiment] = useState<'Positive' | 'Neutral' | 'Negative'>('Neutral');
  const [impact, setImpact] = useState('');
  const [submitting, setSubmitting] = useState(false);


<CategoryPanel selectedCategory={activeTab === 'feed' ? 'Countries' : activeTab} />
  const categories = ['Countries', 'Finance', 'Technology', 'Security', 'Health'];

  async function handleSubmit() {
    if (!headline || !summary) return alert('Headline and Summary are required.');

    setSubmitting(true);
    try {
      const { error } = await supabase.from('intel_feed').insert([
        {
          headline,
          summary,
          region,
          category: category.toLowerCase(),
          sentiment_level: sentiment,
          impact_score: Number(impact) || 0,
          confidence_score: Math.floor(Math.random() * 100),
          access_level: 'Public',
        },
      ]);
      if (error) throw error;

      setHeadline('');
      setSummary('');
      setRegion('');
      setImpact('');
      setSentiment('Neutral');
      alert('‚úÖ Intel submitted successfully!');
    } catch (error) {
      console.error('Submit error:', error);
      alert('‚ö†Ô∏è Submission failed.');
    } finally {
      setSubmitting(false);
    }
  }

  // üåà Dynamic background gradient based on sentiment
  const getSentimentColors = (sentiment: string) => {
    switch (sentiment) {
      case 'Positive':
        return {
          from: 'rgba(34,197,94,0.25)',
          to: 'rgba(34,197,94,0.05)',
        };
      case 'Negative':
        return {
          from: 'rgba(239,68,68,0.25)',
          to: 'rgba(239,68,68,0.05)',
        };
      default:
        return {
          from: 'rgba(251,191,36,0.25)',
          to: 'rgba(251,191,36,0.05)',
        };
    }
  };

  const sentimentColors = getSentimentColors(sentiment);

  return (
    <main className="min-h-screen bg-[#020617] text-white flex flex-col items-center p-6 relative overflow-hidden">
      {/* ===== Header Section ===== */}
      <div className="text-center mb-6">
        <NeuralHalo status="Stable" />
        <h1 className="text-3xl font-bold text-blue-400 drop-shadow-md mt-3">
          A.I.V.E. Command Center
        </h1>
        <p className="text-gray-400 text-sm mt-1 tracking-widest">
          Awareness ‚Ä¢ Intelligence ‚Ä¢ Verification ‚Ä¢ Evolution
        </p>
      </div>

      {/* ===== Feed Panel ===== */}
      <div className="w-full max-w-4xl mb-8">
        <IntelFeed />
      </div>

      {/* ===== Trend Panel ===== */}
      <div className="w-full max-w-4xl">
        <TrendPanel />
      </div>

      {/* ===== Collapsible Form ===== */}
      <motion.div
        className="fixed bottom-5 left-5 w-80 border border-slate-800 rounded-xl shadow-lg overflow-hidden backdrop-blur-xl"
        initial={{ y: 120, opacity: 0 }}
        animate={{ y: formOpen ? 0 : 120, opacity: formOpen ? 1 : 0.7 }}
        transition={{ duration: 0.5 }}
      >
        {/* Dynamic glow background */}
        <motion.div
          className="absolute inset-0 blur-2xl"
          animate={{
            background: [
              `radial-gradient(circle at 50% 50%, ${sentimentColors.from}, transparent 60%)`,
              `radial-gradient(circle at 60% 40%, ${sentimentColors.to}, transparent 70%)`,
            ],
          }}
          transition={{
            duration: 2.5,
            repeat: Infinity,
            ease: 'easeInOut',
            repeatType: 'mirror',
          }}
        ></motion.div>

        {/* Header Toggle */}
        <div
          onClick={() => setFormOpen(!formOpen)}
          className="relative bg-gradient-to-r from-blue-700 to-blue-500 px-4 py-2 flex justify-between items-center cursor-pointer z-10"
        >
          <span className="text-sm font-semibold tracking-wide">
            {formOpen ? '‚ñ≤ Hide Intelligence Form' : '‚ñº Show Intelligence Form'}
          </span>
          <span className="text-xs text-slate-200">A.I.V.E. Input</span>
        </div>

        <AnimatePresence>
          {formOpen && (
            <motion.div
              className="relative z-10 p-4 flex flex-col gap-3 text-sm bg-slate-900/90"
              initial={{ opacity: 0, y: 10 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: 10 }}
              transition={{ duration: 0.3 }}
            >
              {/* Category Tabs */}
              <div className="flex gap-2 overflow-x-auto pb-1">
                {categories.map((c) => (
                  <button
                    key={c}
                    onClick={() => setCategory(c)}
                    className={`px-3 py-1.5 rounded-full text-xs font-medium transition ${
                      category === c
                        ? 'bg-blue-600 text-white shadow shadow-blue-700/40'
                        : 'bg-slate-800 text-slate-300 hover:bg-slate-700'
                    }`}
                  >
                    {c}
                  </button>
                ))}
              </div>

              {/* Input Fields */}
              <input
                type="text"
                placeholder="Region (e.g., USA, Europe)"
                value={region}
                onChange={(e) => setRegion(e.target.value)}
                className="w-full bg-slate-800 text-slate-200 p-2 rounded-md border border-slate-700 focus:ring-2 focus:ring-blue-500"
              />
              <input
                type="text"
                placeholder="Headline"
                value={headline}
                onChange={(e) => setHeadline(e.target.value)}
                className="w-full bg-slate-800 text-slate-200 p-2 rounded-md border border-slate-700 focus:ring-2 focus:ring-blue-500"
              />
              <textarea
                placeholder="Summary or Key Details..."
                rows={3}
                value={summary}
                onChange={(e) => setSummary(e.target.value)}
                className="w-full bg-slate-800 text-slate-200 p-2 rounded-md border border-slate-700 focus:ring-2 focus:ring-blue-500"
              ></textarea>

              <div className="flex justify-between items-center gap-2">
                <select
                  value={sentiment}
                  onChange={(e) => setSentiment(e.target.value as 'Positive' | 'Neutral' | 'Negative')}
                  className="bg-slate-800 border border-slate-700 text-slate-200 rounded-md p-1.5 text-xs w-1/3"
                >
                  <option>Neutral</option>
                  <option>Positive</option>
                  <option>Negative</option>
                </select>

                <input
                  type="number"
                  placeholder="Impact (0‚Äì100)"
                  value={impact}
                  onChange={(e) => setImpact(e.target.value)}
                  className="bg-slate-800 border border-slate-700 text-slate-200 rounded-md p-1.5 text-xs w-1/3 text-center"
                />

                <button
                  onClick={handleSubmit}
                  disabled={submitting}
                  className={`${
                    submitting ? 'bg-blue-400' : 'bg-blue-600 hover:bg-blue-500'
                  } px-3 py-1.5 text-xs rounded-md font-semibold text-white transition w-1/3`}
                >
                  {submitting ? 'Sending‚Ä¶' : 'Submit'}
                </button>
              </div>
            </motion.div>
          )}
        </AnimatePresence>
      </motion.div>

      {/* ===== Footer ===== */}
      <footer className="mt-10 text-xs text-slate-600 text-center z-10">
        ¬© {new Date().getFullYear()} eX Intelligence Systems ‚Ä¢ Powered by A.I.V.E.
      </footer>
    </main>
  );
}
