'use client';
import { useEffect, useState } from 'react';
import { createClient } from '@supabase/supabase-js';
import { motion } from 'framer-motion';

const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL!;
const supabaseAnonKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!;
const supabase = createClient(supabaseUrl, supabaseAnonKey);

interface Trend {
  region: string;
  sentiment_level: string;
  impact_score: number;
  confidence_score: number;
  count: number;
}

export default function TrendPanel() {
  const [trends, setTrends] = useState<Trend[]>([]);
  const [glowSequence, setGlowSequence] = useState<string[]>([]);

  // Fetch & aggregate
  useEffect(() => {
    async function loadTrends() {
      const { data } = await supabase
        .from('intel_feed')
        .select('region, sentiment_level, impact_score, confidence_score');
      if (!data) return;

      const grouped: { [key: string]: Trend } = {};
      data.forEach((item: any) => {
        if (!grouped[item.region]) {
          grouped[item.region] = {
            region: item.region,
            sentiment_level: item.sentiment_level,
            impact_score: 0,
            confidence_score: 0,
            count: 0,
          };
        }
        const g = grouped[item.region];
        g.impact_score += item.impact_score || 0;
        g.confidence_score += item.confidence_score || 0;
        g.count++;
      });

      const aggregated = Object.values(grouped).map((g) => ({
        ...g,
        impact_score: Math.round(g.impact_score / g.count),
        confidence_score: Math.round(g.confidence_score / g.count),
      }));

      setTrends(aggregated);
    }

    loadTrends();

    // üåä Listen for new intel and create ripple wave
    const subscription = supabase
      .channel('intel_feed_ripple')
      .on(
        'postgres_changes',
        { event: 'INSERT', schema: 'public', table: 'intel_feed' },
        (payload) => {
          const region = payload.new.region || '';
          const sentiment = payload.new.sentiment_level || 'Neutral';

          // üåà Send pulse to the Halo
          window.dispatchEvent(new CustomEvent('aive-pulse', { detail: { sentiment } }));

          // Trigger the ripple animation
          triggerRipple(region);
          // Refresh data after delay
          setTimeout(loadTrends, 500);
        }
      )
      .subscribe();

    return () => supabase.removeChannel(subscription);
  }, []);

  // üåä Sequential glow propagation
  function triggerRipple(epicenter: string) {
    const allRegions = trends.map((t) => t.region);
    const centerIndex = allRegions.indexOf(epicenter);

    if (centerIndex === -1) return;

    // create a ripple expanding outward from the center region
    const sequence: string[] = [];
    for (let i = 0; i < allRegions.length; i++) {
      const offset = Math.abs(centerIndex - i);
      sequence.push(allRegions[i]);
      setTimeout(() => {
        setGlowSequence((prev) => [...prev, allRegions[i]]);
        setTimeout(() => {
          setGlowSequence((prev) => prev.filter((r) => r !== allRegions[i]));
        }, 700);
      }, offset * 150);
    }
  }

  const sentimentColor = (s: string) => {
    if (s === 'Positive') return 'text-emerald-400';
    if (s === 'Negative') return 'text-rose-400';
    return 'text-amber-400';
  };

  const glowColor = (s: string) => {
    if (s === 'Positive') return 'rgba(45,212,191,0.6)';
    if (s === 'Negative') return 'rgba(239,68,68,0.6)';
    return 'rgba(251,191,36,0.6)';
  };

  return (
    <div className="bg-slate-900/80 rounded-xl p-4 border border-slate-800 shadow-inner mt-6">
      <h2 className="text-sky-400 font-semibold mb-3 flex items-center gap-2">
        üåç Global Trend Panel
      </h2>

      <div className="grid md:grid-cols-2 gap-3">
        {trends.map((trend) => {
          const isGlowing = glowSequence.includes(trend.region);
          const glow = glowColor(trend.sentiment_level);

          return (
            <motion.div
              key={trend.region}
              className={`relative p-3 rounded-lg bg-slate-800 border border-slate-700`}
              animate={
                isGlowing
                  ? {
                      boxShadow: [
                        `0 0 20px ${glow}`,
                        `0 0 0 ${glow}`,
                      ],
                    }
                  : { boxShadow: '0 0 0 rgba(0,0,0,0)' }
              }
              transition={{
                duration: 0.7,
                ease: 'easeInOut',
              }}
            >
              <div className="flex justify-between items-center mb-1">
                <p className={`font-semibold ${sentimentColor(trend.sentiment_level)}`}>
                  {trend.region}
                </p>
                <span className="text-xs text-slate-400">{trend.sentiment_level}</span>
              </div>
              <p className="text-xs text-slate-400">
                Confidence {trend.confidence_score}% ‚Ä¢ Impact {trend.impact_score} ‚Ä¢ {trend.count}{' '}
                entries
              </p>
              <div className="mt-2 h-1 bg-slate-700 rounded-full overflow-hidden">
                <motion.div
                  className={`h-1 ${
                    trend.sentiment_level === 'Positive'
                      ? 'bg-emerald-400'
                      : trend.sentiment_level === 'Negative'
                      ? 'bg-rose-500'
                      : 'bg-amber-400'
                  }`}
                  initial={{ width: 0 }}
                  animate={{ width: `${trend.confidence_score}%` }}
                  transition={{ duration: 1.5 }}
                />
              </div>
            </motion.div>
          );
        })}
      </div>

      <p className="text-[10px] text-slate-600 text-center mt-3">
        Live data from Supabase ‚Ä¢ Neural ripple propagation active
      </p>
    </div>
  );
}
