'use client';
import { useState, useEffect } from 'react';
import { createClient } from '@supabase/supabase-js';

const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL!;
const supabaseAnonKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!;
const supabase = createClient(supabaseUrl, supabaseAnonKey);

export default function IntelFeed() {
  const [intel, setIntel] = useState<any[]>([]);
  const [category, setCategory] = useState('All');

  const categories = [
    'All',
    'Countries',
    'Finance',
    'Military Action',
    'Outbreaks',
    'Global Intel Agencies',
  ];

  // Load intel based on category
  useEffect(() => {
    async function loadIntel() {
      let query = supabase.from('intel_feed').select('*').order('created_at', { ascending: false });

      // üß† Optional: filter by category (if you store categories in intel_feed)
      if (category !== 'All') query = query.eq('category', category.toLowerCase());

      const { data, error } = await query;
      if (!error && data) setIntel(data);
    }

    loadIntel();

    const subscription = supabase
      .channel('intel_feed_updates')
      .on('postgres_changes', { event: 'INSERT', schema: 'public', table: 'intel_feed' }, () => {
        loadIntel();
      })
      .subscribe();

    return () => supabase.removeChannel(subscription);
  }, [category]);

  return (
    <div className="bg-slate-900/70 rounded-xl p-4 border border-slate-800 shadow-lg mt-6">
      <div className="flex items-center justify-between mb-3">
        <h2 className="text-sky-400 font-semibold flex items-center gap-2">
          üß† Live Intel Feed
        </h2>
        {/* üîΩ Category Dropdown */}
        <select
          value={category}
          onChange={(e) => setCategory(e.target.value)}
          className="bg-slate-800 text-slate-200 text-sm rounded-md px-3 py-1 border border-slate-700 hover:border-sky-500 focus:ring-1 focus:ring-sky-400 focus:outline-none transition"
        >
          {categories.map((cat) => (
            <option key={cat} value={cat}>
              {cat}
            </option>
          ))}
        </select>
      </div>

      <div className="space-y-3">
        {intel.map((item) => (
          <div
            key={item.id}
            className={`p-3 rounded-lg border-l-4 ${
              item.sentiment_level === 'Positive'
                ? 'border-emerald-400 bg-emerald-900/10'
                : item.sentiment_level === 'Negative'
                ? 'border-rose-400 bg-rose-900/10'
                : 'border-amber-400 bg-amber-900/10'
            }`}
          >
            <p className="text-slate-300 text-sm font-semibold">{item.region}</p>
            <p className="text-slate-100 font-bold text-md mt-1">{item.headline}</p>
            <p className="text-slate-400 text-sm">{item.summary}</p>
            <p className="text-xs italic text-slate-500 mt-1">
              Sentiment: {item.sentiment_level}
            </p>
          </div>
        ))}
      </div>
    </div>
  );
}
